.conteudo {
    position: relative;

    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
}

.background {
    height: 100vh;
    position: absolute;
    left: 0;
}

.info {
    width: 60%;
    margin-right: 90px;
    margin-top: 40px;
    color: #FFFFFF;
}

input {
    border: 1px solid #999999;
    border-radius: 22px;
    color: #FFFFFF;
    min-width: 250px;
    width: 85%;
    margin-right: 12px;
    background: transparent;
    font-size: 18px;
    padding: 10px;

}

button {
    border: 1px solid #999999;
    border-radius: 22px;
    margin-right: 12px;
    background: #2D333B;
    color: #FFFFFF;
    font-size: 18px;
    padding: 10px;

}

.perfil {
    display: flex;
    margin: 40px 0;
}

.profile {
    width: 120px;
    height: 120px;
    border-radius: 60px;
    border: 2px solid #E5E5E5;
    margin-right: 32px;
}

.perfil h3 {
    color: #999999;
    font-size: 24px;
    margin: 0;
}

.perfil p {
    color: #999999;
    font-size: 12px;
    margin: 20px 0 0 0;
}

.perfil span {
    display: block;
    color: #999999;
    font-size: 12px;
    margin: 0;
}

.repositorio {
    font-size: 32px;
    color: #FFFFFF;
    margin: 12px 0;
    text-align: center;
}

@media screen and (max-width: 768px) {

    input {
        width: 100%;
        margin-right: 0;
        margin-bottom: 12px;
        font-size: 16px;
        position: relative; 
        z-index: 1; /* Coloque o input acima de outros elementos */
    }

    button {
        width: 100%;
        margin-right: 0;
        font-size: 16px;
        padding: 12px;
        position: relative; 
        z-index: 2; /* Ajuste o valor de z-index */
    }

  }